.header {
    background: #43414C;
    color: #fff;
    padding: 10px;
    width: 100%;
}
.pointer {
    cursor: pointer;
}
.tabs {
    cursor: pointer;
    background: #FCC797;
    text-transform: uppercase;
}
.icons {
    width: 80px;
    height: auto;
}
.items {
    background-color: #f4f4f4;
    padding: 5px;
    border-bottom: 1px #ccc dotted;
}
.padding {
    padding-left: 50px;
}
.btn-add {
    margin-left: auto;
    margin-right: 35px;
    margin-top: 16px;
    border-radius: 50%;
    border: none;
    padding: 10px;
} 
.input {
    margin-top: 55px;
    margin-left: 20px;
    margin-right: 25px;
    width: 40%;
    border: none;
    border-bottom: 2px solid #8c8c8c;
}
.bg {
    width: 40%;
    background: #53bab8;
    border-style: none;
    font-size: 20px;
}
.btn-none {
    border: none;
    background-color: white;
}
.order {
    margin-top: 50px;
}
body {
  font-family: 'PT Sans', sans-serif;
  margin: 0;
  padding: 0;
}
